.joinChat__container{   
    width: 500px;
    height: 600px;
    border-radius: 15px;
    background-color:rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 20px;
}
.logo__container{
    /* border: 1px solid red; */
    padding: 0 50px;
    height: 200px;
    margin-top: -80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.logo{
    height: 60px;
    filter: invert(48%) sepia(79%) saturate(1800%) hue-rotate(20deg) brightness(40%) contrast(110%);
}
.title{
    font-size: 40px;
    color: rgb(215, 189, 154);
}
.nameInput, .roomInput{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 65%;
    /* border: 2px solid red; */
}
.nameInput input, .roomInput input{
    width: 100%;
    padding: 15px 5px;
    /* border-radius:10px; */
    border: none;
    background: none;
    border-bottom: 1px solid rgb(215, 189, 154);
    font-size: 20px;
    outline: none;
}
.nameInput span, .roomInput span{
    font-weight: 500;
    font-size: 18px;
    align-self: self-start;
    color: rgb(202, 182, 155);
}

.roomInput{
margin-top: 20px;
}
.btnContainer{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 30px;
}

.joinButton{
    position: absolute;
    margin-top: 40px;
    padding: 10px;
    width: 50%;
    border-radius: 10px;
    border: none;
    transition: all .3s ;
    z-index: 2;
    font-size: 20px;
    background-color:antiquewhite;
    color: rgb(81, 81, 81);
}
.joinButton:hover{
    margin-top: 63px;
    margin-left: 10px;
    background-color: rgb(252, 230, 201);
}
.underButton{
    padding: 20px;
    width: 50%;
    border-radius: 10px;
    border: none;
    position: absolute;
    margin-top: 65px ;
    margin-left: 10px;
    background-color: rgb(81, 81, 81);
}


@media(max-width:670px){
    .joinChat__container{   
        height: 500px;
    }
    .title{
        font-size: 40px;
        display: inline-block;
    }
    .logo{
        height: 60px;
        margin-top: 30px;
        
    }
}