.chat__container{
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 15px;
    /* border: 2px solid red; */
    background-color: rgb(254, 255, 255);
}
.chat_header{
    /* border: 1px solid red; */
    padding: 0px 30px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap:10px
}
.chat_header p{
    font-size: 36px;
    color: rgb(215, 189, 154);
}
.chat_header img{
    height: 30px;
    filter: invert(48%) sepia(79%) saturate(1500%) hue-rotate(20deg) brightness(40%) contrast(190%);
}
.chat_body{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 80%;
    text-align: center;
    border-top: 1px solid rgb(202, 182, 155);
    border-bottom: 1px solid rgb(202, 182, 155);
}

.messagesContainer{
    display: flex;
    flex-direction: column;
    padding: 5px 5px;
}
.messagesScroller{
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
.message_block_author{
    padding: 0px 10px;
    margin: 5px 5px 5px 0px;
    display: flex;
    justify-content: flex-end;
}
.message_block_friend{
    padding: 0px 10px;
    margin: 5px 5px 5px 0px;
    display: flex;
    justify-content: flex-start;
}

.messagePosition_author{
    display: inline-block;
    padding: 10px 20px;
    border-radius: 12px;
    background-color: rgb(255, 232, 197);
}
.messagePosition_friend{
    display: inline-block;
    padding: 10px 20px;
    border-radius: 12px;
    background-color: rgb(235, 235, 235);
}

.message_author{
    word-wrap: break-word;
    font-size: 24px;
    text-align: end;
}
.message_friend{
    word-wrap: break-word;
    font-size: 24px;
    text-align: start;
}
.message_info_author{
    font-weight: bold;
    font-size: 15px;
    margin-top: 5px;
    display: flex;
    gap: 5px;
    display: flex;
    justify-content: end;
    font-style: italic;
    opacity: 0.80;
}
.message_info_friend{
    font-weight: bold;
    font-size: 15px;
    margin-top: 5px;
    display: flex;
    gap: 5px;
    display: flex;
    justify-content: start;
    font-style: italic;
    opacity: 0.80;
}

.chat__footer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 12%;
}
.textInput{
    width: 80%;
    height: 30%;
    resize: none;
    outline: none;
    border: 1px solid grey;
    background-color: none;
    border-radius: 15px;
    padding: 15px 20px;
    font-size: 16px;
    filter: brightness(105%);
    margin-left: 15px;
}

.sendButton{
    background: none;
    border: none;
    cursor: pointer;
    filter: invert(48%) sepia(79%) saturate(50%) hue-rotate(190deg) brightness(70%) contrast(90%);
}
.sendButton img{
    padding: 12px 15px;
    height: 40px;
    width: 40px;
    filter: invert(48%) sepia(79%) saturate(50%) hue-rotate(190deg) brightness(70%) contrast(90%);
    opacity: 0.5;
}
.sendButton img:hover{
    opacity: 1;
}

@media(max-width:670px){
    .message_author,.message_friend{
        font-size: 20px;
    }
    .message_info_author,.message_info_friend{
        font-size: 13px;
    }
    .message_info_author{
        display: flex;
        justify-content: end;
        
    }
    .message_info_friend{
        display: flex;
        justify-content: start;
    }
}