body{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: rgb(214, 213, 212);
    box-sizing: border-box;
    margin: 0px auto;
    font-family: sans-serif;
}

.appContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    width: 800px;
    height: 600px;
    min-height: 650px;
}

@media(max-width:850px){
    .appContainer{
        width: 600px;
    }
}
@media(max-width:670px){
    .appContainer{
        width: 370px;
    }
}
